body {
    font: 20px "Century Gothic", Futura, sans-serif;
    margin: 100px;
  }
  
  ol, ul {
    padding-left: 0px;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    margin-bottom: 10px;
  }
  
  .square {
    background: #fff;
    border: 3px solid #939bb0;
    float: left;
    font-size: 50px;
    font-weight: bold;
    line-height: 34px;
    height: 100px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 100px;
  }

  .moveButton {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 15px;
    color: #4a5593;
    line-height: 34px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 10;
    text-align: center;
    width: 200px;
  }

  .boardBorder {
    border: 100px solid transparent;
    padding: 0px;
    border-image: url(flowers.png) 20% round;
  }
  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: flex;
    flex-direction: row;
  }
  
  .game-info {
    margin-left: 20px;
  }
  